.my-token-page {
  height: 100%;
}
.my-token-page .head-tip {
  width: 100%;
  height: 2.75rem;
  line-height: 2.75rem;
  background: #1b1d21;
  text-align: center;
}
.my-token-page .head-tip span {
  font-size: 1rem;
  font-weight: 400;
  color: #ffffff;
}
.my-token-page .token-contain {
  padding: 0.9375rem;
}
.my-token-page .token-contain .title {
  font-size: 1.125rem;
  margin-bottom: 0.9375rem;
  font-weight: 400;
  color: #ffffff;
}
.my-token-page .token-contain .token-item-border {
  border: 1px solid !important;
  border-image: linear-gradient(90deg, #ff2ef3, #4f00ff) 30 30 !important;
}
.my-token-page .token-contain .token-item {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 8.75rem;
  margin-bottom: 0.9375rem;
  background: linear-gradient(-30deg, #333333, #111111);
  border: 1px solid #666666;
}
.my-token-page .token-contain .token-item .mark-tip-colour,
.my-token-page .token-contain .token-item .mark-tip-gray {
  position: absolute;
  top: 0.625rem;
  left: -1.25rem;
  width: 5rem;
  height: 1.25rem;
  font-size: 0.75rem;
  line-height: 1.25rem;
  background: linear-gradient(90deg, #00e4ff, #8400ff);
  transform: rotate(-45deg);
  text-align: center;
  color: #ffffff;
  font-weight: 400;
}
.my-token-page .token-contain .token-item .mark-tip-colour {
  background: linear-gradient(90deg, #00e4ff, #8400ff);
}
.my-token-page .token-contain .token-item .mark-tip-gray {
  background: linear-gradient(90deg, #555555, #333333);
}
.my-token-page .token-contain .token-item .item-contain {
  padding-left: 2.1875rem;
  padding-right: 2.1875rem;
  padding-top: 1.5625rem;
  height: 5.3125rem;
}
.my-token-page .token-contain .token-item .item-contain .item:nth-child(2),
.my-token-page .token-contain .token-item .item-contain .item:nth-child(3) {
  margin-top: 0.9375rem;
}
.my-token-page .token-contain .token-item .item-contain .item {
  display: flex;
  justify-content: space-between;
  color: #ffffff;
}
.my-token-page .token-contain .token-item .item-contain .item .goto {
  color: #00D2FF;
}
.my-token-page .token-contain .token-item .item-btn {
  height: 1.875rem;
  line-height: 1.875rem;
  text-align: center;
  color: #ffffff;
  position: relative;
}
.my-token-page .token-contain .token-item .item-btn:before {
  content: " ";
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  height: 1px;
  border-top: 1px solid #666666;
  color: #666666;
  transform-origin: 0 0;
  transform: scaleY(0.5);
}
.my-token-page .token-contain .token-item .item-btn .blue {
  color: #00D2FF;
}
.my-token-page .token-contain .token-item .item-btn-no {
  height: 1.875rem;
  line-height: 1.875rem;
  text-align: center;
  color: #666666;
  position: relative;
}
.my-token-page .token-contain .token-item .item-btn-no:before {
  content: " ";
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  height: 1px;
  border-top: 1px solid #666666;
  color: #666666;
  transform-origin: 0 0;
  transform: scaleY(0.5);
}
.my-token-page .no-data .image {
  display: flex;
  height: 11.875rem;
  justify-content: center;
  align-items: center;
}
.my-token-page .no-data .image img {
  width: 5.09375rem;
  height: 5.1875rem;
}
.my-token-page .no-data .des {
  display: flex;
  height: 1.25rem;
  font-size: 1.0625rem;
  justify-content: center;
  align-items: center;
  font-weight: 400;
  color: #ffffff;
}
.my-token-page .no-data .link {
  display: flex;
  height: 0.9375rem;
  font-size: 0.6875rem;
  margin-top: 0.625rem;
  justify-content: center;
  align-items: center;
  font-weight: 400;
  color: #00D2FF;
}
